import { BASE_IMAGE_URL,getGoodsList,jiaruCart } from '@/utils/api'
import uniPopup from '@/components/uni-popup.vue'
import uniLoadMore from '@/components/uni-load-more.vue';
export default {
	components: {
		uniLoadMore,
		uniPopup
	},
	data() {
		return {
			loadingText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			},
			list: [],
			loadingType: 0,
			page: 1,
			typeList: ['综合', '最新', '价格', '库存'],
			curType: 0,
			g_type: 0,
			type:'',
			companyCode: ''
		}
	},
	methods: {
		hidePop(){
			this.type="";
		},
		async bindCompanyCode(){
			let params = {
				k_g_id: this.list[this.curType].g_id,
				k_m_id: uni.getStorageSync("userInfo").m_id,
				k_yj_value: this.companyCode
			};
			
			let res = await setKucunYujing(params);
			if(res.code == 1000){
				uni.showToast({
				  icon: 'none',
				  title: '设置成功',
				  duration: 1000
				});
				this.list[this.curType].g_kucun = this.companyCode;
			}
			this.type="";
			
		},
		setYuJing(index){
			console.log(index);
			this.curType = index;
			this.type = 'middle';
		},
		async getList(){
			let res = await getGoodsList(this.g_type,this.page, this.curType, '');
			uni.stopPullDownRefresh();
			if(res.code == 1000){
				if(this.page == 1){
					this.list = res.data;
				}else{
					this.list = this.list.concat(res.data);
				}
				
				if(res.data.length < 10){
					this.loadingType = 2;
				}else{
					this.loadingType = 0;
				}
			}
		},
		goToDetail(index){
			uni.navigateTo({
				url: '/pages/goods_detail/goods_detail?g_id='+this.list[index].g_id
			});
		},
		async jiaruCart(item){
			let params = {
				ct_openid: uni.getStorageSync("openid"),
				ct_g_id: item.g_id,
			};
			let res = await jiaruCart(params);
			if(res.code == 1000){
				uni.showToast({
				  icon: 'none',
				  title: '添加成功',
				  duration: 1000
				});
			}else{
				uni.showToast({
				  icon: 'none',
				  title: '加入购物车失败',
				  duration: 1000
				});
			}
		},
	},
	onReachBottom() {
		this.loadingType = 1;
		this.page++;
		this.getList();
	},
	
	onPullDownRefresh() {
		console.log("dddddd");
		this.page = 1;
		this.list = [];
		this.getList();
	},
	onLoad(options){
		this.g_type = options.g_type;
		this.getList();
	}
	
}
